import './DrumMachine.scss'
import {React,Component} from 'react'

const activePadColor = '#7bed9f';
const inactivePadColor = '#dfe4ea';

const activeStyle = {
  backgroundColor: activePadColor,
}

const inactiveStyle = {
  backgroundColor: inactivePadColor,
  boxShadow: '3px 3px 5px black'
}


class Pad extends Component {
  constructor(props) {
    super(props)
    
    this.state = {
      padStyle: inactiveStyle
    }

    this.playAudio = this.playAudio.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.activatePad = this.activatePad.bind(this);
  }

  componentDidMount() {
    document.addEventListener('keydown', this.handleKeyPress);
  }
  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyPress);
  }

  activatePad() {
    if (this.state.padStyle.backgroundColor === activePadColor) {
      this.setState({
        padStyle: inactiveStyle
      });
    } else {
      this.setState({
        padStyle: activeStyle
      });
    }
  }

  playAudio() {
    if (this.props.power === 'on') {
      const sound = document.getElementById(this.props.keyTrigger);
      sound.currentTime = 0;
      sound.volume = this.props.volume;
      sound.play();
      this.activatePad();
      setTimeout(() => this.activatePad(), 100);
      this.props.updateDisplay(this.props.clipId.replace(/-/g, ' '));
    }
  }

  handleKeyPress(e) {
    if (e.keyCode === this.props.keyCode) {
      this.playAudio();
    }
  }

  render() {
    return(
      <div className="drumPad" onClick={this.playAudio} style={this.state.padStyle}>
        <p>{this.props.keyTrigger}</p>
        <audio id={this.props.keyTrigger} src={this.props.sound}></audio>
      </div>
    )
  }
}

class PadBank extends Component {
  constructor(props) {
    super(props)
  }

  render() {
    let padBank = this.props.bank.map(obj => {
      return (
        <Pad 
          keyTrigger = {obj.keyTrigger}
          sound = {obj.url}
          keyCode = {obj.keyCode}
          power = {this.props.power}
          clipId = {obj.id}
          updateDisplay={this.props.updateDisplay}
          volume = {this.props.volume}
        />
      )
    })

    return padBank;
  }
}

class DrumMachine extends Component {
  constructor(props) {
    super(props)
    this.state = {
      power: "off",
      display: String.fromCharCode(160),
      sliderVal: 0.5,
      bank: bankOne
    }

    this.adjustVolume = this.adjustVolume.bind(this);
    this.displayClipName = this.displayClipName.bind(this);
    this.powerClick = this.powerClick.bind(this);
    this.clearDisplay = this.clearDisplay.bind(this);
    this.selectBank = this.selectBank.bind(this);
  }

  displayClipName(name) {
    if (this.state.power === "on") {
      this.setState({
        display: name
      });
    }
  }

  powerClick() {
    if (this.state.power === "on") {
      document.getElementById("power-outer-Id").style.justifyContent = 'flex-start';
      document.getElementById("power-inner-Id").style.backgroundColor = '#e84118';
      this.clearDisplay();
      this.setState({
        power: 'off'
      });
    } else {
      document.getElementById("power-outer-Id").style.justifyContent = 'flex-end';
      document.getElementById("power-inner-Id").style.backgroundColor = '#4cd137';
      this.setState({
        power: 'on'
      });
    }
    
  }

  adjustVolume(e) {
    if (this.state.power === "on") {
      this.setState({
        sliderVal: e.target.value,
        display: 'Volume: ' + Math.round(e.target.value * 100)
      });
      setTimeout(() => this.clearDisplay(), 100);
    }
  }

  selectBank() {
    if (this.state.power === "on") {
      if (this.state.bank === bankOne) {
        this.setState({
          bank: bankTwo,
          display: "Smooth Piano Kit"
        });

      document.getElementById("bankOuterId").style.justifyContent = 'flex-end';
      document.getElementById("bankInnerId").style.backgroundColor = '#4cd137';

      } else {
        this.setState({
          bank: bankOne,
          display: "Heater Kit"
        });

      document.getElementById("bankOuterId").style.justifyContent = 'flex-start';
      document.getElementById("bankInnerId").style.backgroundColor = '#e84118';
      }
    }
  }

  clearDisplay() {
    this.setState({
      display: String.fromCharCode(160)
    });
  }

  render() {
    return(
      <div className="container-outer">
        <div className="container-inner">
          <div className="padBankContainer">
            <PadBank bank={this.state.bank} power={this.state.power} 
            updateDisplay={this.displayClipName} volume={this.state.sliderVal}/>
          </div>

          <div className="controller">
            <div className="power">
              Power
              <div id="power-outer-Id" className="power-outer">
                <div id="power-inner-Id" className="power-inner" onClick={this.powerClick}>
                  
                </div>
              </div>
            </div>

            <div className="display">
              <div className="display-outer">
                <div className="display-inner">
                  {this.state.display}
                </div>
              </div>
            </div>

            <div className="volumeBar">
              <input
              max='1'
              min='0'
              onChange={this.adjustVolume}
              step='0.01'
              type='range'
              value={this.state.sliderVal}
            />
            </div>

            <div className="bank">
              Bank
              <div className="bank-outer" id="bankOuterId">
                <div className="bank-inner" id="bankInnerId" onClick={this.selectBank}>

                </div>
              </div>
            </div>

          </div>
          
        </div>

        <div className="author">
          By Giga Vardia
        </div>
      </div>
    )
  }
}





















export default DrumMachine;

